import "./index.scss";
import data from "./data";
import { audioControl, iconControl, volumeControl } from "./musicControl";
import { volumeUI } from "./components/volume";
import { imgIcon } from "./components/imgIcon";
import { toggleMusicUI } from "./components/toggleMusic";
var root = document.querySelector("#app");
var background = document.querySelector(".background");
var title = document.createElement("h1");
var togglePanel = document.createElement("div");
var defaultButtonActive = 0;
function handleVolume(event) {
    var audioCurrent = event.target.childNodes[0];
    audioCurrent.volume = event.target.value;
}
function handleChangePreset(event) {
    event.preventDefault();
    var currentId = event.target.id;
    var audioState = audioControl(event);
    volumeControl(event);
    iconControl({ audioState: audioState, currentId: currentId });
    background.style.backgroundImage = "url(".concat(data[currentId].bgImage, ")");
    title.style.color = data[currentId].color;
}
function createButton(item, index) {
    var toggleWrapper = document.createElement("div");
    toggleWrapper.className = "toggleWrapper";
    var toggleMusic = toggleMusicUI(index);
    toggleMusic.onclick = function (event) { return handleChangePreset(event); };
    toggleWrapper.appendChild(toggleMusic);
    var volume = volumeUI({ index: index, defaultButtonActive: defaultButtonActive });
    volume.oninput = function (event) { return handleVolume(event); };
    toggleWrapper.appendChild(volume);
    var icon = imgIcon(index);
    toggleMusic.appendChild(icon);
    return toggleWrapper;
}
function init() {
    background.style.backgroundImage = "url(".concat(data[defaultButtonActive].bgImage, ")");
    title.textContent = "Weather sounds";
    title.className = "title";
    title.style.color = data[defaultButtonActive].color;
    root.appendChild(title);
    togglePanel.className = "togglePanel";
    root.appendChild(togglePanel);
    data.forEach(function (item, index) {
        var toggleWrapper = createButton(item, index);
        togglePanel.appendChild(toggleWrapper);
    });
}
init();
